import styles from "../Styles/styles";
import { Card } from "react-native-paper";
import { View } from "react-native";
import Icons from "react-native-vector-icons/FontAwesome5";

const Lista = ({ item, cardPress, iconPress, borrar }) => {

  return (
    <Card onPress={() => cardPress(item.id)} style={styles.card}>
      <Card.Title
        title={item.nombre}
        subtitle={item.subnombre}
        left={() => (
          <LeftContent
            click={iconPress}
            completa={item.completa}
            id={item.id}
            borrar={borrar}
          />
        )}
      />
    </Card>
  );
};

const LeftContent = ({ completa, id, click,borrar }) => (
  <View style={styles.icons}>
    <Icons
      size={40}
      name={borrar ? "trash" : completa ? "check" : "hourglass"}
      onPress={() => click(id)}
    />
  </View>
);

export default Lista;
