import { useState } from "react";
import { TextInput } from "react-native-paper";
import {
  SafeAreaView,
  View,
  Text,
  ScrollView,
  Alert,
  Platform,
} from "react-native";

import Icons from "react-native-vector-icons/FontAwesome5";
import styles from "../Styles/styles";

const EditarLista = ({ listaInd, data, setData, setVista, setListaInd }) => {
  const [nombre, setnombre] = useState(
    listaInd === null ? "" : listaInd.nombre
  );
  const [subnombre, setsubnombre] = useState(
    listaInd === null ? "" : listaInd.subnombre
  );
  const [contenido, setcontenido] = useState(
    listaInd === null ? "" : listaInd.articulos.map((r) => r.nombre).join("\n")
  );
  const [articulos, setarticulos] = useState(
    listaInd === null ? [] : listaInd.articulos.map((r) => r.nombre)
  );

  const alertIos = () => {
    Alert.alert("Guardar", nombre, [
      {
        text: "Cancelar",
        style: "cancel",
      },
      { text: "Aceptar", onPress: saveLista() },
    ]);
  };

  const alertAndroid = () => {
    Alert.alert(
      "Guardar",
      nombre,
      [
        {
          text: "Cancelar",
          style: "cancel",
        },
        {
          text: "Aceptar",
          onPress: saveLista(),
        },
      ],
      {
        cancelable: true,
      }
    );
  };

  const nombreChangeHandler = (value) => {
    setnombre(value);
  };
  const subnombreChangeHandler = (value) => {
    setsubnombre(value);
  };

  const contenidoChangeHandler = (value) => {
    setcontenido(value);
    setarticulos(value.split("\n").filter((v) => v !== ""));
  };

  const tryToSave = () => {
    switch (Platform.OS) {
      case "android":
        alertAndroid();
        break;

      case "ios":
        alertIos();
        break;

      case "web":
        if (confirm("¿Guardar Lista?") === true) {
          saveLista();
        }
    }
  };

  const saveLista = () => {

    listaInd !== null
      ? setData(
          data.map((l) =>
            l.id === listaInd.id
              ? {
                  id: l.id,
                  nombre: nombre === "" ? "Lista sin nombre" : nombre,
                  subnombre: subnombre,
                  completa: false,
                  articulos: articulos.map((art) => ({
                    nombre: art,
                    completa:
                      l.articulos.find((artFind) => artFind.nombre === art) !== undefined
                        ? l.articulos.find((artFind) => artFind.nombre === art).completa
                        : false,
                  })),
                }
              : l
          )
        )
      : setData([
          ...data,
          {
            id: data.length > 0 ? data[data.length - 1].id + 1 : 1,
            nombre: nombre === "" ? "Lista sin nombre" : nombre,
            subnombre: subnombre,
            completa: false,
            articulos: articulos.map((art) => ({
              nombre: art,
              completa: false,
            })),
          },
        ]);
  };

  const goBack = () => {
    listaInd === null ? setVista("menu") : setVista("verLista");
    if (listaInd !== null) setListaInd(data.find((d) => d.id === listaInd.id));
  };

  return (
    <SafeAreaView style={styles.background}>
      {/* Titulo Pagina */}
      <View style={styles.baseText}>
        <Icons size={30} name={"chevron-left"} onPress={goBack} />
        <Text style={styles.titleText}>
          {listaInd === null ? "Crear lista" : `Editar ${listaInd.nombre}`}
        </Text>
        <Icons size={30} name={"save"} onPress={tryToSave} />
      </View>
      {/* Editar/Crear lista */}
      <ScrollView style={styles.item}>
        <TextInput
          style={styles.item}
          label={"Nombre"}
          value={nombre}
          onChangeText={nombreChangeHandler}
        />
        <TextInput
          style={styles.item}
          label={"Subnombre"}
          value={subnombre}
          onChangeText={subnombreChangeHandler}
        />

        <TextInput
          style={[styles.item, styles.textBox]}
          label={"Articulos"}
          value={contenido}
          onChangeText={contenidoChangeHandler}
          multiline={true}
          numberOfLines={18}
        />
      </ScrollView>
    </SafeAreaView>
  );
};
export default EditarLista;
