import { useState } from "react";
import { SafeAreaView, View, Text, ScrollView } from "react-native";
import styles from "../Styles/styles";
import Icons from "react-native-vector-icons/FontAwesome5";
import { Searchbar } from "react-native-paper";
import Lista from "./Lista";

const VerLista = ({ lista, setvista, data, setData, editarLista }) => {

  const [search, setsearch] = useState("");
  const [filteredData, setFilteredData] = useState(
    data.find((l) => l.id === lista.id).articulos
  );

  const onChangeSearch = (query) => {
    setsearch(query);

    if (query !== "") {
      setFilteredData(
        filteredData.filter((l) =>
          l.nombre.toUpperCase().match(query.toUpperCase())
        )
      );
    } else {
      setFilteredData(lista.articulos);
    }
  };

  const togglecompletaClick = (nombre) => {
    const tempData = data.map((l) =>
      l.nombre === lista.nombre
        ? {
            ...l,
            articulos: l.articulos.map((art) =>
              art.nombre === nombre ? { ...art, completa: !art.completa } : art
            ),
          }
        : l
    );

    setData(tempData);
    setFilteredData(tempData.find((l) => l.id === lista.id).articulos);
  };

  return (
    <>
      <SafeAreaView style={styles.background}>
        {/* Titulo Pagina */}
        <View style={styles.baseText}>
          <Icons
            size={30}
            name={"chevron-left"}
            onPress={() => setvista("menu")}
          />
          <Text style={styles.titleText}>{lista.nombre}</Text>
          <Icons size={30} name={"edit"} onPress={editarLista} />
        </View>
        {/* Barra Busqueda */}
        <View style={styles.item}>
          <Searchbar
            placeholder="Buscar"
            value={search}
            onChangeText={onChangeSearch}
          />
        </View>
        {/* Listas */}
        <View style={styles.listas}>
          <View style={[styles.item, styles.listaInd]}>
            {filteredData.map((item, index) =>
              !item.completa ? (
                <Lista
                  cardPress={() => togglecompletaClick(item.nombre)}
                  iconPress={() => togglecompletaClick(item.nombre)}
                  item={item}
                  key={index}
                  borrar={false}
                />
              ) : null
            )}
          </View>
          <View style={[styles.item, styles.listaInd]}>
            {filteredData.map((item, index) =>
              item.completa ? (
                <Lista
                  cardPress={() => togglecompletaClick(item.nombre)}
                  iconPress={() => togglecompletaClick(item.nombre)}
                  item={item}
                  key={index}
                  borrar={false}
                />
              ) : null
            )}
          </View>
        </View>
      </SafeAreaView>
    </>
  );
};
export default VerLista;
