import { useState, useEffect } from "react";

import { ScrollView, Text, View, SafeAreaView, Platform } from "react-native";

import { Searchbar } from "react-native-paper";
import AsyncStorage from "@react-native-async-storage/async-storage";

import Icons from "react-native-vector-icons/FontAwesome5";

import Lista from "./source/Components/Lista";
import styles from "./source/Styles/styles";
import VerLista from "./source/Components/VerLista";
import EditarLista from "./source/Components/EditarLista";

export default function App() {
  const [search, setsearch] = useState("");
  const [vista, setvista] = useState("menu");
  const [listas, setlistas] = useState([]);
  const [filteredData, setFilteredData] = useState([]);
  const [listaInd, setlistaInd] = useState({});
  const [deleteState, setdeleteState] = useState(false);
  const [firstRender, setfirstRender] = useState(true);

  const setData = async (jsonValue) => {
    try {
      setlistas(jsonValue != null ? JSON.parse(jsonValue) : null);
      setFilteredData(jsonValue != null ? JSON.parse(jsonValue) : null);
    } catch (e) {
      console.log(e);
    }
  };

  const saveData = async (data) => {
    try {
      const jsonValue = JSON.stringify(data);
      await AsyncStorage.setItem("@listas", jsonValue);
    } catch (e) {
      console.log(e);
    }
  };

  useEffect(() => {
    if (!firstRender) {
      saveData(listas);
    }
  }, [listas]);

  useEffect(() => {
    getData();
  }, []);

  const getData = async () => {
    try {
      const value = await AsyncStorage.getItem("@listas");
      if (value !== null && value !== undefined) {
        setData(value);
      } else {
        saveData([]);
      }
      setfirstRender(false)
    } catch (error) {
      console.log(error);
    }
  };

  const onChangeSearch = (query) => {
    setsearch(query);

    if (query !== "") {
      setFilteredData(
        listas.filter((l) => l.nombre.toUpperCase().match(query.toUpperCase()))
      );
    } else {
      setFilteredData(listas);
    }
  };

  const toggleCompletaClick = (id) => {
    const tempData = listas.map((l) =>
      l.id === id
        ? {
            ...l,
            completa: !l.completa,
            articulos: l.articulos.map(
              (art) => (art = { ...art, completa: !l.completa })
            ),
          }
        : l
    );

    setlistas(tempData);
    setFilteredData(tempData);
  };

  const verEditar = (nueva) => {
    if (nueva) setlistaInd(null);
    setvista("editar");
  };

  const toggleBorrarCheck = () => {
    setdeleteState(!deleteState);
  };

  const verLista = (id) => {
    setlistaInd(listas.find((i) => i.id === id));
    setvista("verLista");
  };

  const borrarLista = (id) => {
    setlistas(listas.filter((l) => l.id !== id));
    setFilteredData(listas.filter((l) => l.id !== id));
  };

  if (search === "" && filteredData.length !== listas.length) {
    setFilteredData(listas);
  }

  return (
    <>
      {vista === "menu" ? (
        <SafeAreaView style={styles.background}>
          {/* Titulo Pagina */}
          <View style={styles.baseText}>
            <Icons
              size={30}
              name={deleteState === true ? "ban" : "minus"}
              onPress={toggleBorrarCheck}
            />
            <Text style={styles.titleText}>{"Listas"}</Text>
            <Icons size={30} name={"plus"} onPress={() => verEditar(true)} />
          </View>
          {/* Barra Busqueda */}
          <View style={styles.item}>
            <Searchbar
              placeholder="Buscar"
              value={search}
              onChangeText={onChangeSearch}
            />
          </View>

          {/* Listas */}
          <ScrollView style={styles.item}>
            {filteredData.map((lista, index) => (
              <Lista
                cardPress={deleteState ? () => borrarLista(lista.id) : verLista}
                iconPress={
                  deleteState
                    ? () => borrarLista(lista.id)
                    : () => toggleCompletaClick(lista.id)
                }
                item={lista}
                key={index}
                borrar={deleteState}
              />
            ))}
          </ScrollView>
        </SafeAreaView>
      ) : vista === "verLista" ? (
        <VerLista
          lista={listaInd}
          setvista={setvista}
          data={listas}
          setData={setlistas}
          editarLista={() => verEditar(false)}
        />
      ) : vista === "editar" ? (
        <EditarLista
          listaInd={listaInd}
          data={listas}
          setData={setlistas}
          setVista={setvista}
          setListaInd={setlistaInd}
        />
      ) : (
        <View></View>
      )}
    </>
  );
}
